/* For BookList.jsx */
.booklist {
  background-color: #fafafa;
  display: flex;
  flex-direction: column;
  margin: 2% 9%;
}

/* For Book.jsx */
.bookContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-radius: 4px;
  border: 1px solid #e8e8e8;
  margin: 2%;
  background-color: #fff;
  padding: 1%;
}

.bookInnerContainer {
  display: flex;
  flex-direction: column;
  margin: 2% 0;
}

.bookButtons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.divider {
  border: 1px solid #e0e0e0;
  margin: 0 5%;
}

.bookButton {
  color: #0290ff;
  font-family: Montserrat, sans-serif;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: 1.9px;
  align-items: center;
  justify-content: center;
  list-style: none;
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
}

.bookTitle {
  height: 1.813rem;
  font-family: RobotoSlab, sans-serif;
  font-size: 1.375rem;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: -0.2px;
  color: #121212;
}

.bookAuthor {
  margin: 0.15rem 0 0;
  font-family: RobotoSlab, sans-serif;
  font-size: 0.875rem;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #4386bf;
}

.bookCategory {
  width: 3.063rem;
  height: 1.125rem;
  margin: 0 2.438rem 0 0;
  opacity: 0.5;
  font-family: Montserrat, sans-serif;
  font-size: 0.875rem;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #121212;
}

.bookDetails {
  display: flex;
  flex-direction: column;
  margin: 5%;
  gap: 15px;
}

.ovalContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 5% 5%;
}

.oval {
  width: 4.25rem;
  height: 4.25rem;
  border-width: 5px;
  border-image-source: linear-gradient(to bottom, var(--medium-blue), #379cf6);
  border-image-slice: 1;
  padding: 0.63rem 0.625rem 0.75rem 0.75rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ovalInner {
  position: absolute;
  border-radius: 50%;
  width: 4.25rem;
  height: 4.25rem;
  border: 5px solid #307bbe;
  border-left-color: #e8e8e8;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.progressInfo {
  display: flex;
  flex-direction: column;
  margin: 5% 5%;
  color: black;
  margin-left: 10px;
}

.currentContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 4.9% 0;
}

.currentChapter {
  width: 7.875rem;
  height: 1.125rem;
  opacity: 0.5;
  font-family: Roboto, sans-serif;
  font-size: 0.813rem;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #121212;
}

.currentChapterTitle {
  width: auto;
  height: 1.313rem;
  font-family: Roboto, sans-serif;
  font-size: 1rem;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: -0.4px;
  color: #121212;
}

.updateButton {
  font-family: Roboto, sans-serif;
  font-size: 0.813rem;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: 0.5px;
  width: 11.5rem;
  height: 2.063rem;
  border-radius: 3px;
  background-color: #0290ff;
  color: white;
  align-items: center;
  justify-content: center;
  list-style: none;
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
}

.percentage {
  width: 4.25rem;
  height: 2.438rem;
  margin: 0.063rem 5.75rem 0 0.688rem;
  font-family: Montserrat, sans-serif;
  font-size: 2rem;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #121212;
}

.completed {
  width: 5rem;
  height: 1.125rem;
  margin: 0.438rem 4.938rem 0.75rem 0.75rem;
  opacity: 0.5;
  font-family: Montserrat, sans-serif;
  font-size: 0.875rem;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #121212;
}
